import { Component, OnInit, Input, ViewChild, Output, EventEmitter, ElementRef, AfterViewInit, OnChanges } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatMenuService } from '../shared/services';
import { Observable } from 'rxjs';
import { NgOnChangesFeature } from '@angular/core/src/render3';

@Component({
  selector: 'dlv-mat-waybill-search',
  template: `
    <div class="waybill">
        <form [formGroup]="waybillForm">


            <div class="waybill-search">
                <span class="material-icons">search</span>
                <input #waybillInput type="text" formControlName="waybillNo" (keyup.enter)="searchWaybill()" placeholder="{{placeHolder}}" name="waybillNo" />
            
            </div>

        </form>
    </div>
  `,
  styles: [`
    .waybill{position:relative;margin:5px 10px}.waybill .waybill-search{position:relative;background:rgba(0,0,0,0.06);width:100%;min-width:200px;border-radius:3px}.waybill .waybill-search input{box-shadow:none !important;border-radius:0;border:1px solid transparent;font-size:13px;line-height:21px;color:#000;background:transparent;padding:8px 8px 8px 38px;border-radius:5px;width:calc(100% - 38px)}.waybill .waybill-search input:focus{outline:none}.waybill .waybill-search span{position:absolute;left:14px;top:9px;color:#939696;font-size:22px}.waybill .waybill-search span.has-error{color:#ff0000;pointer-events:none}.waybill .errMsg{margin:4px 0;color:red;font-size:12px}@media screen and (max-width: 768px){.waybill{margin:0;padding:6px 10px 6px 6px;background:#fff;box-shadow:inset 1px 1px 4px rgba(0,0,0,0.18)}.waybill .waybill-search{min-width:150px !important}}
  `]
})
export class DlvMatWaybillComponent implements OnInit, OnChanges  {

  @Input () placeHolder : string;
  @Input() isAutoFocus: boolean;
  waybillForm : FormGroup;
  waybillNo : FormControl;
  @ViewChild('waybillInput') waybillInputRef: ElementRef;

  private waybills : Array<any> = [];
  // pattern = "([A-Z]{1,3}[0-9]{10,12})(\s*,\s*([A-Z]{1,3}\d{10,12}))*$"

  constructor(private menuService: MatMenuService) { }
  private isFocusEvent: any

  ngOnInit() {
    this.createFormControls();
    this.createForm();
  }

  ngOnChanges() {
     if(this.isAutoFocus) {
       setTimeout(()=>{
          this.waybillInputRef.nativeElement.focus();
       }, 0)
    }
  }
/**
 * @name createFormControls
 * @description This method is assigning value to input field
 */

  private activateFocus() {
    this.waybillInputRef.nativeElement.focus();
  }

  private createFormControls(){
    this.waybillNo = new FormControl('', []);
  }

  /**
   * @name createForm
   * @description It is containing waybill no
   */

  private createForm(){
    this.waybillForm = new FormGroup({
        waybillNo : this.waybillNo,
    });
  }
  /**
   * @name searchWaybill
   * @description this method is using to get the searched waybill detail on seperate window.
   */
  public searchWaybill(){
    let waybill = this.waybillForm.value;
    let urlEndPoint = this.menuService.getUrlLink()[this.menuService.getOptions().environment];
    window.open(`${urlEndPoint}p/list/1?q=${waybill.waybillNo}`);

  }

  ngOnDestroy() {
    this.isFocusEvent.unsubscribe()
  }

}